import React from 'react';
import { Link } from 'react-router-dom';
import './home.css';
import Header from './../../components/header/header';

export default class Home extends React.Component {

    newsList = [{
        text: '习近平视察陆军步兵学院',
        src: '/detail'
    }, {
        text: '习近平在江西考察调研',
        src: '/video-detail'
    }, {
        text: '习近平会见第六次全国自强模范暨自残先进表彰',
        src: '/detail'
    }, {
        text: '习近平和彭丽媛同出席亚洲文明对话大会的外方',
        src: '/detail'
    }, {
        text: '习近平出席亚洲文明对话大会开幕式并发表主旨',
        src: '/detail'
    }, {
        text: '习近平会见亚美尼亚总理帕希尼杨',
        src: '/detail'
    }];

    handleFocus() {

    }

    render() {
        return (
            <div>
                <Header />
                <div className="newsList">
                    <ul>
                        {
                            this.newsList.map((item, index) => {
                                return (
                                    <li key={index}>
                                        <Link to={item.src} tabIndex={index + 10} className="newsItem" onFocus={this.handleFocus.bind(this)}>
                                        <img className="picture" src={require('./../../assets/images/home/image0'+ (index + 1) +'.png')} alt={item.text}/>
                                        <div className="desc">
                                            <span>{item.text}</span>
                                            <img className="arrow" src={require('./../../assets/images/arrow.png')} alt=""/>
                                        </div>
                                        </Link>
                                    </li>
                                )
                            })
                        }
                    </ul>
                </div>
            </div>
        )
    }
}